.logo,
.logo:hover {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  text-align: start;
}

.logo span {
  font-size: 18px;
  margin: 0 15px;
}

body:lang(en) .logo span {
  font-family: "Cormorant Garamond", serif;
  font-size: 22px;
}

.img-responsive {
  max-width: 100%;
}
